import {Pipe} from "@angular/core";
import {BaseHealthState, VsanHealthUtil} from "@util/vsan-health-util";

@Pipe({
   name: "healthStateLabelClass",
})
export class HealthStateLabelClass {
   transform(state: BaseHealthState): string {
      return VsanHealthUtil.getHealthStateLabelClass(state);
   }
}